import styled from "styled-components"
import {DownloadIcon} from "@chakra-ui/icons";
import resume from "../assets/resume/Resume.pdf"

export const Resume = () => {
    return (
        <RESUME id="resume">
           <button onClick={() => {
              window.open("https://drive.google.com/file/d/1japUtT9hj4apgviRwskoTr544G_LD7y1/view?usp=sharing", "_blank")
            }} className="contact-btn" type="button" id="resume-button-1">
              <a id="resume-link-1" href={resume} rel='noopener noreferrer' download="ashwini_titarmare_resume">
                Resume
                <DownloadIcon color="white" />
              </a>
            </button>
        </RESUME>
    )
}

const RESUME = styled.div`
margin: 100px auto;
display: flex;
justify-content: center;
 #resume-button-1 {
      background-color:transparent;
      border: 1px solid transparent;
      color: white;
      font-size: 25px;
      font-weight: 600;
    }
       .contact-btn {
  text-decoration: none;
  background-color: var(--color-primary);
  color: var(--color-text);
  border-radius: 100px;
  font-size: 30px;
  font-weight: 600;
  padding: 10px 26px 15px 26px;
  box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.25);
}
`